export class SimpleDate {
  constructor(label = '') {
    if (label.length >= 10) {
      const day   = label.substr(3, 2);
      const month = label.substr(0, 2);
      const year  = label.substr(6, 4);

      this.iso   = `${year}-${month}-${day}`;
      this.year  = parseInt(year);
      this.month = parseInt(month);
      this.day   = parseInt(day);
    }

    this.label = label;
  };

  equals(date2) {
    return this.year === date2.year && this.month === date2.month && this.day === date2.day;
  };

  isGreaterThan(date2) {
    return (this.year > date2.year) ||
           (this.year === date2.year && this.month > date2.month) ||
           (this.year === date2.year && this.month === date2.month && this.day > date2.day);
  };

  isGreaterThanOrEqualTo(date2) {
    return this.isGreaterThan(date2) || this.equals(date2);
  };
};

export const convertToSimpleDate = date => {
  const day   = date.getDate().toString().padStart(2, '0');
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const year  = date.getFullYear();

  return new SimpleDate(`${month}/${day}/${year}`);
}
