import React from 'react';

class AddItemForm extends React.PureComponent {
  render() {
    let itemTypeIcon;

    switch (this.props.itemTypeId) {
      default:
      case 0:
        itemTypeIcon = 'film';
        break;
      case 1:
        itemTypeIcon = 'book-open';
        break;
      case 2:
        itemTypeIcon = 'book';
        break;
    }

    return (
      <form id="addItemForm" onSubmit={this.props.addItem} className="flex mb5">
        <div className="bdrBox mr5 flex80">
          <div className="flex">
            <input type="text" name="title" placeholder="title" value={this.props.title} onChange={this.props.handleItemChange} className="bdrBox mr5 mb5 bdrGray p5 flex100"/>
            <div id="cycleItemTypeButton" onClick={this.props.cycleItemType} className="flex justifyCenter alignCenter bdrBox bdrGray p5 txtC csrPointer">
              <i aria-hidden={true} className={'fas fa-' + itemTypeIcon + ' csrPointer'}></i>
            </div>
          </div>
          <div className="flex">
            <div id="date" className="flex mr5">
              <div className="mr5 flex33">
                <input type="text" name="month" maxLength="2" placeholder="month" value={this.props.month} onChange={this.props.handleItemChange} className="bdrBox bdrGray p5 flex100"/>
              </div>
              <div className="mr5 flex33">
                <input type="text" name="day" maxLength="2" placeholder="day" value={this.props.day} onChange={this.props.handleItemChange} className="bdrBox bdrGray p5 flex100"/>
              </div>
              <div className="flex33">
                <input type="text" name="year" maxLength="2" placeholder="year" value={this.props.year} onChange={this.props.handleItemChange} className="bdrBox bdrGray p5 flex100"/>
              </div>
            </div>
            <div id="toggleItemFirstButton" onClick={this.props.toggleItemIsFirst} className="flex justifyCenter alignCenter bdrBox bdrGray p5 lineHieght1 csrPointer">{(this.props.isFirst) ? 'X' : ''}</div>
          </div>
        </div>
        <button type="submit" id="addItemButton" className="bdrBox bdrGray p5 flex20 csrPointer">add</button>
      </form>
    );
  }
}

export default AddItemForm;
