import React from 'react';

import { SimpleDate } from '../utils';

import('../css/calendar.css');

class Calendar extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      daysOfTheWeek: [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday'
      ],
      monthsOfTheYear: [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
      ]
    }
  };

  checkForItems = (itemsOfDay, date) => {
    if (itemsOfDay.length > 0) {
      const day   = date.getDate().toString().padStart(2, '0');
      const month = (date.getMonth() + 1).toString().padStart(2, '0')
      const year  = date.getFullYear().toString();
      const simpleDate = new SimpleDate(`${month}/${day}/${year}`);

      this.props.onClick(simpleDate);
    }
  };

  render() {
    const daysInMonth     = new Date(this.props.year, this.props.month + 1, 0).getDate();
    const firstDayInMonth = new Date(this.props.year, this.props.month, 1).getDay();
    const lastDayOnPage   = (firstDayInMonth + daysInMonth) % 7 > 0 ? (Math.floor((firstDayInMonth + daysInMonth) / 7) + 1) * 7 : 0;

    let labels       = [];
    let daysBefore   = [];
    let daysAfter    = [];
    let days         = [];
    let itemsInMonth = [];

    this.props.items.forEach(item => {
      if (item.date.year === this.props.year && item.date.month === this.props.month + 1) {
        itemsInMonth.push(item);
      }
    });


    for (let i = 0; i < this.state.daysOfTheWeek.length; i++) {
      labels.push(
        <div className="day bdrBox mb5 bdrBlack p5 txtC truncate" key={this.state.daysOfTheWeek[i]}>
          {this.state.daysOfTheWeek[i]}
        </div>
      );
    }

    for (let i = 0; i < firstDayInMonth; i++) {
      daysBefore.push(
        <div className="day bdrBox mb5 bdrGray p5" key={`before${i}`}></div>
      );
    }

    for (let i = 0; i < daysInMonth; i++) {
      const dayOfMonth = i + 1;
      const date  = new Date(this.props.year, this.props.month, dayOfMonth);
      const itemsOfDay = [];

      itemsInMonth.forEach((item, index) => {
        if (item.date.day === dayOfMonth) {
          let icon = 'book';

          if (item.type === 'comic') {
            icon = 'book-open';
          } else if (item.type === 'movie') {
            icon = 'film';
          }

          itemsOfDay.push(<i aria-hidden={true} className={`mr5 fas fa-${icon}`} key={index}></i>);
        }
      });

      days.push(
        <div
        className={`day bdrBox mb5 bdrBlack p5 ${itemsOfDay.length > 0 ? 'csrPointer' : ''}`}
        key={`day${i}`}
        onClick={() => {this.checkForItems(itemsOfDay, date)}}
        >
          <p>{i + 1}</p>
          <p>{itemsOfDay}</p>
        </div>
      );
    }

    for (let i = firstDayInMonth + daysInMonth; i < lastDayOnPage; i++) {
      daysAfter.push(
        <div className="day bdrBox mb5 bdrGray p5" key={`after${i}`}></div>
      );
    }

    return (
      <section id="calendar">
        <div className="flex spaceBetween alignCenter bdrBox mb5 bdrBlack p5">
          <i aria-hidden={true} className={`mr5 fs14 fas fa-chevron-circle-left csrPointer`} onClick={this.props.setMonthPrevious}></i>
          <p className="mr5">{this.state.monthsOfTheYear[this.props.month]} {this.props.year}</p>
          <i aria-hidden={true} className={`fs14 fas fa-chevron-circle-right csrPointer`} onClick={this.props.setMonthNext}></i>
        </div>
        <div className="flex flex-wrap bdrBox">
          {labels}
          {daysBefore}
          {days}
          {daysAfter}
        </div>
      </section>
    );
  }
}

export default Calendar;
