import React from 'react';

class Filters extends React.PureComponent {
  checkDate = (event) => {
    let date = event.target.value;

    if (date.length < 10) {
      this.props.clearDate(event);
    }
  };

  render() {
    const sortButton           = this.props.isAsc          ? 'fa-caret-square-up' : 'fa-caret-square-down';
    const bookButtonMode       = this.props.showBooks      ? '' : 'txtRed';
    const comicButtonMode      = this.props.showComics     ? '' : 'txtRed';
    const movieButtonMode      = this.props.showMovies     ? '' : 'txtRed';
    const dateButtonMode       = this.props.showDate       ? '' : 'txtRed';
    const duplicatesButtonMode = this.props.showDuplicates ? '' : 'txtRed';

    return (
      <div className="flex flex-col m-flex-row spaceBetween alignCenter mb5 fs14">
        <div className="flex alignCenter mr5 mb5 m-mb0 flex100 m-flex1">
          <i aria-hidden={true} className={`mr5 fas ${bookButtonMode} fa-book csrPointer`} onClick={this.props.toggleShowBooks}></i>
          <i aria-hidden={true} className={`mr5 fas ${comicButtonMode} fa-book-open csrPointer`} onClick={this.props.toggleShowComics}></i>
          <i aria-hidden={true} className={`mr5 fas ${movieButtonMode} fa-film csrPointer`} onClick={this.props.toggleShowMovies}></i>
          <i aria-hidden={true} className={`mr5 fas ${dateButtonMode} fa-calendar csrPointer`} onClick={this.props.toggleShowDate}></i>
          <i aria-hidden={true} className={`mr5 fas ${duplicatesButtonMode} fa-clone csrPointer`} onClick={this.props.toggleShowDuplicates}></i>
          <i aria-hidden={true} className={`mr5 fas ${sortButton} csrPointer`} onClick={this.props.toggleIsAsc}></i>
        </div>
        <div className="flex flex100 m-flex1">
          <div className="mr5 flex33 m-w100">
            <input
            className="bdrBox bdrBlack p5 flex100 txtC"
            id="dateFrom"
            max="10"
            name="dateFrom"
            onBlur={this.checkDate}
            onChange={this.props.filterDate}
            onClick={this.props.clearDate}
            placeholder="from: mmddyyyy"
            value={this.props.dateFrom.label}
            />
          </div>
          <div className="mr5 flex33 m-w100">
            <input
            className="bdrBox bdrBlack p5 flex100 txtC"
            id="dateTo"
            max="10"
            name="dateTo"
            onBlur={this.checkDate}
            onChange={this.props.filterDate}
            onClick={this.props.clearDate}
            placeholder="to: mmddyyyy"
            value={this.props.dateTo.label}
            />
          </div>
          <div className="flex33 m-w100">
            <input
            className="bdrBox bdrBlack p5 flex100"
            name="title"
            onChange={this.props.filterTitle}
            placeholder="title"
            value={this.props.title}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Filters;
