import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import md5 from 'md5';

import AddItemForm from './components/AddItemForm';
import Calendar from './components/Calendar';
import Filters from './components/Filters';
import Items from './components/Items';
import SignInForm from './components/SignInForm';

import { SimpleDate, convertToSimpleDate } from './utils';

import('./css/library.css');

class App extends Component {
  constructor(props) {
    super(props);

    const params = this.parsePath();
    const today  = new Date();

    this.state = {
      calendarMonth: today.getMonth(),
      calendarYear: today.getFullYear(),
      dateFrom: params.dateFrom,
      dateTo: params.dateTo,
      input: {
        username: '',
        password: '',
      },
      isAsc: params.isAsc,
      item: {
        day: today.getDate().toString().padStart(2, '0'),
        isFirst: true,
        month: (today.getMonth() + 1).toString().padStart(2, '0'),
        title: '',
        type : 'movie',
        year : today.getFullYear(),
      },
      items: [],
      itemsFiltered: [],
      itemTypeId: 0,
      itemTypes: [
        'movie',
        'comic',
        'book'
      ],
      showAddItemForm: false,
      showBooks: params.showBooks,
      showCalendar: params.showCalendar,
      showComics: params.showComics,
      showDate: params.showDate,
      showDuplicates: params.showDuplicates,
      showFilters: params.showFilters,
      showMovies: params.showMovies,
      showSignInForm: false,
      today,
      timezoneOffset: today.getTimezoneOffset() / 60,
      title: params.title,
      user: this.getCookie()
    };
  };

  componentDidMount() {
    this.getItems();
  };

  addItem = (event) => {
    event.preventDefault();

    const data = {
      username: this.state.user.name,
      md5:      this.state.user.md5,
      item: {
        date:    this.state.item.year + '-' + this.state.item.month + '-' + this.state.item.day,
        isFirst: this.state.item.isFirst,
        title:   this.state.item.title,
        type:    this.state.itemTypes[this.state.itemTypeId]
      }
    };

    axios.get('https://www.rootbeercomics.com/scripts/list/insert.php', {
      params: {
        data: data
      }
    }).then(response => {
      if (response.data.success) {
        let item = this.state.item;

        item.title = '';

        this.setState({item});
        this.getItems();
      }
    });
  }

  clearDate = (event) => {
    this.setState({
      [event.target.name]: new SimpleDate()
    }, this.setItemsFiltered);
  };

  clearPath = () => {
    this.setState({
      dateFrom: '',
      dateTo: '',
      isAsc: false,
      showAddItemForm: false,
      showBooks: true,
      showCalendar: false,
      showComics: true,
      showDate: true,
      showDuplicates: true,
      showFilters: false,
      showMovies: true,
      showSignInForm: false,
      title: ''
    }, this.setItemsFiltered);
  };

  convertDate = datetime => {
    // Compensate for required iOS format.
    let date = new Date(datetime.substr(0, 10));

    // Compensate for data lacking timezone.
    date.setHours(date.getHours() + this.state.timezoneOffset);

    return date;
  };

  cycleItemType = () => {
    const itemTypeId = (this.state.itemTypeId + 1 >= this.state.itemTypes.length) ? 0 : this.state.itemTypeId + 1;

    this.setState({itemTypeId});
  };

  filterDate = (event) => {
    let date = event.target.value.match(/\d/g);

    date = date ? date.slice(0, 8).join('') : '';

    if (date.length > 4) {
      date = date.slice(0, 4) + '/' + date.slice(4);
    }

    if (date.length > 2) {
      date = date.slice(0, 2) + '/' + date.slice(2);
    }

    this.setState({
      [event.target.name]: new SimpleDate(date)
    }, this.setItemsFiltered);
  };

  filterItems = (items) => {
    if (this.state.dateFrom.iso) {
      items = items.filter(item => {
        return item.date.isGreaterThanOrEqualTo(this.state.dateFrom);
      });
    }

    if (this.state.dateTo.iso) {
      items = items.filter(item => {
        return this.state.dateTo.isGreaterThanOrEqualTo(item.date);
      });
    }

    if (!this.state.showDuplicates) {
      items = items.filter(item => item.isFirst);
    }

    if (!this.state.showBooks) {
      items = items.filter(item => item.type !== 'book');
    }

    if (!this.state.showComics) {
      items = items.filter(item => item.type !== 'comic');
    }

    if (!this.state.showMovies) {
      items = items.filter(item => item.type !== 'movie');
    }

    if (this.state.title) {
      items = items.filter(item => {
        return item.title.indexOf(this.state.title) > -1;
      });
    }

    return items;
  };

  filterTitle = (event) => {
    this.setState({
      title: event.target.value
    }, this.setItemsFiltered);
  };

  formatDate = (year, month, day) => {
    const date      = new Date(year, month, day);
    const dateDay   = date.getDate().toString().padStart(2, '0');
    const dateMonth = (date.getMonth() + 1).toString().padStart(2, '0');
    const dateYear  = date.getFullYear().toString().substr(2);

    return `${dateMonth}/${dateDay}/${dateYear}`;
  };

  getCookie = () => {
    const cookies = document.cookie.split('; ');
    let user = {
      isAdmin: false,
      isSignedIn: false,
      md5: null,
      name: null
    };

    cookies.forEach(cookie => {
      const parts = cookie.split('=');
      const key   = parts[0];
      const value = parts[1];

      if (key === 'user' && value !== '') {
        user = JSON.parse(decodeURIComponent(value));
      }
    });

    return user;
  }

  getHeading = () => {
    let heading = `entertainment matt has consumed`;

    if (this.state.showBooks && !this.state.showComics && !this.state.showMovies) {
      heading = 'books matt has read';
    } else if (this.state.showComics && !this.state.showBooks && !this.state.showMovies) {
      heading = 'comics matt has read';
    } else if (this.state.showMovies && !this.state.showBooks && !this.state.showComics) {
      heading = 'movies matt has seen';
    } else if (this.state.showBooks && this.state.showComics && !this.state.showMovies) {
      heading = 'books and comics matt has read';
    }

    return heading;
  }

  getItems = () => {
    axios.get('https://www.rootbeercomics.com/scripts/list/get.php').then(response => {
      const items = response.data.items.map(item => {
      const year  = item.date.substr(0, 4);
      const month = item.date.substr(5, 2);
      const day   = item.date.substr(8, 2);

      item.isFirst = (item.isFirst === '1');
      item.date    = new SimpleDate(`${month}.${day}.${year}`);

        return item;
      });

      this.setState({
        itemsFiltered: this.filterItems(items),
        items
      });
    });
  }

  handleInputChange = (event) => {
    let input = this.state.input;

    input[event.target.name] = event.target.value;

    this.setState({input});
  }

  handleItemChange = (event) => {
    let item = this.state.item;

    item[event.target.name] = event.target.value;

    this.setState({item});
  }

  parsePath = () => {
    let   search    = window.location.search;
    let   urlParams = search.substr(1).split('&').filter(value => value);
    const params    = {
      isAsc:          false,
      dateFrom:       new SimpleDate(),
      dateTo:         new SimpleDate(),
      showBooks:      true,
      showComics:     true,
      showDate:       true,
      showDuplicates: true,
      showFilters:    urlParams.length > 0,
      showMovies:     true,
      title:          ''
    };

    urlParams.forEach(param => {
      if (param === 'asc') {
        params.isAsc = true;
      } else if (param === 'calendar') {
        params.showCalendar = true;
      } else if (param === 'no-books') {
        params.showBooks = false;
      } else if (param === 'no-comics') {
        params.showComics = false;
      } else if (param === 'no-date') {
        params.showDate = false;
      } else if (param === 'no-dupes') {
        params.showDuplicates = false;
      } else if (param === 'no-movies') {
        params.showMovies = false;
      } else if (param.substr(0, 6) === 'start:') {
        params.dateFrom = new SimpleDate(param.substr(6));
      } else if (param.substr(0, 4) === 'end:') {
        params.dateTo = new SimpleDate(param.substr(5));
      } else if (param.substr(0, 6) === 'title:') {
        params.title = param.substr(6);
      }
    });

    return params;
  }

  setCookie = (name, value, expirationDate, path = '/') => {
    if (typeof expirationDate === 'undefined') {
      expirationDate = new Date();
      expirationDate.setHours(expirationDate.getHours() + 24);
    }

    document.cookie = `${name}=${value}; expires=${expirationDate}; path=${path}`;
  }

  setDate = date => {
    this.setState({
      dateFrom: date,
      dateTo: date,
      showFilters: true,
    }, this.setItemsFiltered);
  };

  setItemsFiltered = () => {
    const items = this.state.items.slice();

    this.setPath();
    this.setState({
      itemsFiltered: this.filterItems(items)
    });
  };

  setMonthNext = () => {
    const calendarMonth   = this.state.calendarMonth === 11 ? 0 : this.state.calendarMonth + 1;
    const calendarYear    = this.state.calendarMonth === 11 ? this.state.calendarYear + 1 : this.state.calendarYear;
    const firstDayOfMonth = new Date(calendarYear, calendarMonth, 1);
    const lastDayOfMonth  = new Date(calendarYear, calendarMonth + 1, 0);
    const dateFrom        = convertToSimpleDate(firstDayOfMonth);
    const dateTo          = convertToSimpleDate(lastDayOfMonth);

    this.setState({
      calendarMonth,
      calendarYear,
      dateFrom,
      dateTo
    }, this.setItemsFiltered);
  };

  setMonthPrevious = () => {
    const calendarMonth   = this.state.calendarMonth === 0 ? 11 : this.state.calendarMonth - 1;
    const calendarYear    = this.state.calendarMonth === 0 ? this.state.calendarYear - 1 : this.state.calendarYear;
    const firstDayOfMonth = new Date(calendarYear, calendarMonth, 1);
    const lastDayOfMonth  = new Date(calendarYear, calendarMonth + 1, 0);
    const dateFrom        = convertToSimpleDate(firstDayOfMonth);
    const dateTo          = convertToSimpleDate(lastDayOfMonth);

    this.setState({
      calendarMonth,
      calendarYear,
      dateFrom,
      dateTo
    }, this.setItemsFiltered);
  };

  setPath = () => {
    let params = [];

    if (this.state.isAsc) {
      params.push('asc');
    }

    if (!this.state.showBooks) {
      params.push('no-books');
    }

    if (this.state.showCalendar) {
      params.push('calendar');
    }

    if (!this.state.showComics) {
      params.push('no-comics');
    }

    if (!this.state.showMovies) {
      params.push('no-movies');
    }

    if (!this.state.showDate) {
      params.push('no-date');
    }

    if (!this.state.showDuplicates) {
      params.push('no-dupes');
    }

    if (this.state.dateFrom) {
      params.push('start:' + this.state.dateFrom.label);
    }

    if (this.state.dateTo) {
      params.push('end:' + this.state.dateTo.label);
    }

    if (this.state.title !== '') {
      params.push('title:' + this.state.title);
    }

    this.props.history.push('?' + params.join('&'));
  }

  signIn = (event) => {
    event.preventDefault();

    const hash = md5(this.state.input.username + this.state.input.password);
    const params = {
      username: this.state.input.username,
      md5: hash
    };

    axios.get('https://www.rootbeercomics.com/login/ajax/index.php', {params}).then(response => {
      if (response.data.success) {
        const user = {
          isAdmin: (params.username === 'matt!'),
          isSignedIn: true,
          md5: params.md5,
          name: params.username
        };

        this.setState({
          showSignInForm: false,
          user
        });

        this.setCookie('user', JSON.stringify(this.state.user));
      } else {
        const input = {
          username: this.state.input.username,
          password: ''
        };

        this.setState({input});
      }
    });
  }

  signOut = () => {
    const user = {
      isAdmin: false,
      isSignedIn: false,
      md5: '',
      name: ''
    };

    axios.get('https://www.rootbeercomics.com/login/ajax/log-out.php').then(response => {
      this.setState({
        showAddItemForm: false,
        user
      });
      this.setCookie('user', '');
    });
  }

  toggleFilter = () => {
    const showFilters = !this.state.showFilters;

    this.setState({
      showFilters
    });
  };

  toggleIsAsc = () => {
    this.setState({
      isAsc: !this.state.isAsc
    }, this.setPath);
  };

  toggleItemIsFirst = () => {
    let item = this.state.item;

    item.isFirst = !item.isFirst;

    this.setState({item});
  }

  toggleShowAddItemForm = () => {
    this.setState({
      showAddItemForm: !this.state.showAddItemForm
    });
  };

  toggleShowBooks = () => {
    this.setState({
      showBooks: !this.state.showBooks
    }, this.setItemsFiltered);
  };

  toggleShowCalendar = () => {
    const showCalendar = !this.state.showCalendar;
    let   dateFrom     = new SimpleDate();
    let   dateTo       = new SimpleDate();

    if (showCalendar) {
      const firstDayOfMonth = new Date(this.state.calendarYear, this.state.calendarMonth, 1);
      const lastDayOfMonth  = new Date(this.state.calendarYear, this.state.calendarMonth + 1, 0);

      dateFrom = convertToSimpleDate(firstDayOfMonth);
      dateTo   = convertToSimpleDate(lastDayOfMonth);
    }

    this.setState({
      calendarMonth: this.state.today.getMonth(),
      calendarYear:  this.state.today.getFullYear(),
      dateFrom,
      dateTo,
      showCalendar,
      showFilters: showCalendar ? true : this.state.showCalendar
    }, this.setItemsFiltered);
  };

  toggleShowComics = () => {
    this.setState({
      showComics: !this.state.showComics
    }, this.setItemsFiltered);
  };

  toggleShowDate = () => {
    this.setState({
      showDate: !this.state.showDate
    }, this.setItemsFiltered);
  };

  toggleShowDuplicates = () => {
    this.setState({
      showDuplicates: !this.state.showDuplicates
    }, this.setItemsFiltered);
  };

  toggleShowMovies = () => {
    this.setState({
      showMovies: !this.state.showMovies
    }, this.setItemsFiltered);
  };

  toggleShowSignInForm = () => {
    this.setState({
      showSignInForm: !this.state.showSignInForm
    });
  }

  render() {
    const addItemFormButton = (this.state.user.isAdmin) ? (
      <i aria-hidden={true} className={`mr5 fas fa-edit ${this.state.showAddItemForm ? '' : 'txtRed'} csrPointer`} onClick={this.toggleShowAddItemForm}></i>
    ) : null;

    const signInOutButton = (this.state.user.isSignedIn) ? (
      <i aria-hidden={true} className="fas fa-sign-out-alt csrPointer" onClick={this.signOut}></i>
    ) : (
      <i aria-hidden={true} className="fas fa-sign-in-alt csrPointer" onClick={this.toggleShowSignInForm}></i>
    );

    return (
      <div className="mAuto w600">
        <div className="flex spaceBetween alignCenter mb5 fs14">
          <p className="flex alignCenter mr5 bold">
            <i aria-hidden={true} className={`mr10 fas fa-list-ul csrPointer`} onClick={this.clearPath}></i>
            <span>{this.getHeading()}</span>
          </p>
          <div className="flex">
            {addItemFormButton}
            <i aria-hidden={true} className={`mr5 fas fa-calendar-alt ${this.state.showCalendar ? '' : 'txtRed'} csrPointer`} onClick={this.toggleShowCalendar}></i>
            <i aria-hidden={true} className={`mr5 fas fa-filter ${this.state.showFilters ? '' : 'txtRed'} csrPointer`} onClick={this.toggleFilter}></i>
            {signInOutButton}
          </div>
        </div>
        {this.state.user.isSignedIn && this.state.showAddItemForm &&
          <AddItemForm
          addItem={this.addItem}
          cycleItemType={this.cycleItemType}
          day={this.state.item.day}
          isFirst={this.state.item.isFirst}
          itemTypeId={this.state.itemTypeId}
          handleItemChange={this.handleItemChange}
          month={this.state.item.month}
          title={this.state.item.title}
          toggleItemIsFirst={this.toggleItemIsFirst}
          year={this.state.item.year}
          />
        }
        {this.state.showSignInForm &&
          <SignInForm
          signIn={this.signIn}
          username={this.state.input.username}
          password={this.state.input.password}
          handleInputChange={this.handleInputChange}
          />
        }
        {this.state.showFilters &&
          <Filters
          dateFrom={this.state.dateFrom}
          dateTo={this.state.dateTo}
          clearDate={this.clearDate}
          filterDate={this.filterDate}
          filterTitle={this.filterTitle}
          isAsc={this.state.isAsc}
          params={this.state.params}
          showBooks={this.state.showBooks}
          showDate={this.state.showDate}
          showDuplicates={this.state.showDuplicates}
          showComics={this.state.showComics}
          showMovies={this.state.showMovies}
          title={this.state.title}
          toggleIsAsc={this.toggleIsAsc}
          toggleShowBooks={this.toggleShowBooks}
          toggleShowComics={this.toggleShowComics}
          toggleShowDuplicates={this.toggleShowDuplicates}
          toggleShowMovies={this.toggleShowMovies}
          toggleShowDate={this.toggleShowDate}
          />
        }
        {this.state.showCalendar &&
          <Calendar
          items={this.state.items}
          month={this.state.calendarMonth}
          onClick={this.setDate}
          setMonthNext={this.setMonthNext}
          setMonthPrevious={this.setMonthPrevious}
          year={this.state.calendarYear}
          />
        }
        <Items
        isAsc={this.state.isAsc}
        itemsFiltered={this.state.itemsFiltered}
        showDate={this.state.showDate}
        />
      </div>
    );
  }
}

export default withRouter(App);
